import styled from 'styled-components';

const ProjectStyle = {
    "background-color": "#fff",
    width: "80px",
    height: "80px",
    "border-radius": "100%",
    margin: "10px"
};

export const Project = (props)=> (
    <a href={props.route}>
        <img style={ProjectStyle} src={props.src}></img>
    </a>
);